/*
 * Стили компонента Searchbar
 */
.Searchbar {
  top: 0;
  left: 0;
  position: sticky;
  z-index: 9;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 64px;
  padding-right: 24px;
  padding-left: 24px;
  padding-top: 12px;
  padding-bottom: 12px;
  color: #fff;
  background-color: #01b4e4;
  box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2),
    0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
}

/*
 * Стили компонента SearchForm
 */
.SearchForm {
  display: flex;
  align-items: center;
  width: 100%;
  max-width: 600px;
  background-color: #fff;
  border-radius: 3px;
  overflow: hidden;
}

.SearchForm_button {
  display: inline-block;
  width: 48px;
  height: 49px;
  border: none;
  opacity: 0.6;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
  cursor: pointer;
  outline: none;
}

.SearchForm_button:hover {
  opacity: 1;
}

.searchLogo {
  width: 21px;
  height: 47px;
}

.SearchForm_input {
  display: inline-block;
  padding: 13px 8px;
  width: 100%;
  font: inherit;
  font-size: 20px;
  border: none;
  outline: none;
  color: #01b4e4;
}

.SearchForm_input::placeholder {
  font: inherit;
  font-size: 18px;
}
